* [Reset SCSS]
*/

* {
    -webkit-tap-highlight-color: transparent;
    -webkit-font-smoothing: antialiased;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-text-size-adjust: 100%;
    font-family: $font_n;
    letter-spacing: -0.25px;
  }
  
  html, body {
    height: 100%;
  }
  
  html {
    -webkit-text-size-adjust: none;
    font-size: 62.5%;
  }
  
  body {
    -webkit-text-size-adjust: 100%;  
    min-width: 360px;
    font-size: 13px;
  }
  
  html,
  body,
  div,
  span,
  object,
  iframe,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  blockquote,
  pre,
  abbr,
  address,
  cite,
  code,
  del,
  dfn,
  em,
  img,
  ins,
  kbd,
  q,
  samp,
  small,
  strong,
  sub,
  sup,
  var,
  b,
  i,
  dl,
  dt,
  dd,
  ol,
  ul,
  li,
  fieldset,
  form,
  label,
  legend,
  table,
  caption,
  tbody,
  tfoot,
  thead,
  tr,
  th,
  td,
  article,
  aside,
  canvas,
  details,
  figcaption,
  figure,
  footer,
  header,
  hgroup,
  menu,
  nav,
  section,
  summary,
  time,
  mark,
  audio,
  button,
  select,
  video {
    margin: 0;
    padding: 0;
    background: transparent;
    border: 0;
    outline: 0;
    font-family: $font_n;
    vertical-align: baseline;
  }
  
  h1,
  h2,
  h3,
  h4 {
    margin: 0;
  }
  
  ul,
  ol {
    margin: 0;
    padding: 0;
  }
  
  li {
    list-style: none;
  }
  
  table {
    border-collapse: collapse;
  
    caption {
      display: none;
    }
  }
  
  figure {
    margin: 0;
  }
  
  img {
    max-width: 100%;
    object-fit: contain;
  }
  
  a {
    color: inherit;
    text-decoration: none;
    cursor: pointer;
  }
  
  p {
    margin: 0;
  }
  
  i, em, address {
    font-style: normal;
  }
  
  strong {
    font-weight: inherit;
  }
  
  em {
    font-weight: inherit;
  }
  
  fieldset {
    margin: 0;
    padding: 0;
    border: 0;
    min-width: 0;
  }
  
  legend {
    background-color: transparent;
    border: 0;
    color: transparent;
    font-size: 0;
    text-shadow: none;
  }
  
  button {
    padding: 0;
    border: 0;
    background: none;
    outline: 0;
    user-select: none;
  }
  
  label {
    border: 0;
    cursor: pointer;
  }
  
  input {
    -webkit-appearance: none;
    appearance: none;
    outline: none;
    border: 0;
    border-radius: 0;
    box-sizing: border-box;
    vertical-align: middle;
  
    &::-ms-clear {
      display: none;
    }
  
    &::-webkit-search-cancel-button {
      display: none;
    }
  
    &[type="file"] {
      &::-webkit-file-upload-button {
        opacity: 0;
      }
    }
  
    &[type='number'] {
      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
      }
    }
  }
  
  html body.fb {
    input:focus + span {
      outline: 1px dotted $color999 !important;
    }
  }
  
  select {
    outline: none;
    appearance: none;
    box-sizing: border-box;
    border-radius: 0;
  
    &::-ms-expand {
      display: none;
    }
  
    &:disabled {
      opacity: 1;
    }
  }
  
  textarea {
    resize: none;
    -webkit-border-radius: 0;
    -webkit-appearance: none;
    border-radius: 8px;
    background: $colorfff;
    color: $color111;
    text-indent: 0;
 
    &::placeholder {
        color: $color999;
    }
  }
  
  details {
    summary {
      display: block;
      list-style: none; // 기본css 제거
  
      &::-webkit-details-marker {
        display: none;
      }
    }
  }
  